@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
body {
  margin: 0;
  font-family: 'Poppins',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
}
.signInBtn{
    
     background-color: white;  
    cursor: pointer;
    color: #a80e35;
    max-width: -webkit-fit-content;
    max-width: -moz-fit-content;
    max-width: fit-content;
    padding: 8px 16px;
    border-radius: 5px;
    text-align: center;
    font-size: 14px;
    font-weight: 500;
    margin: 10px;
    box-shadow: 1px 1px rgba(3, 79 , 160, 0.2);
}
.signInBtn:hover{
    background-color: #a80e35;
    color: white;
    transition: all 0.2s;
}
.commentInput{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    
    -webkit-align-items: center;
    
            align-items: center;
}
.commentInput_text{
    border: none;
    resize: none;
    width: 100%;
    font-family: 'Poppins',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
    
  font-size: 14px;
}
.commentInput_text:focus{
    outline: none;
}
.commentInput > button{
    background-color: white;
    border: none;
    padding: 12px;
    cursor: pointer;
}
.signOutBtn{
    
    background-color: white;  
   cursor: pointer;
   color: #a80e35;
   max-width: -webkit-fit-content;
   max-width: -moz-fit-content;
   max-width: fit-content;
   padding: 8px 16px;
   border-radius: 5px;
   text-align: center;
   font-size: 14px;
   font-weight: 500;
   margin: 10px;
   box-shadow: 1px 1px rgba(3, 79 , 160, 0.2);
}
.signOutBtn:hover{
   background-color: #a80e35;
