}
.navbar{
    display: -webkit-flex;
    display: flex;
    padding: 10px;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background-color: silver;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 0.5s;
    -webkit-align-items: center;
            align-items: center;
    
}
.logo{
    -webkit-align-self: center;
            align-self: center;
