    background-color: white;  
    color: #a80e35;
   cursor: pointer;   
   max-width: -webkit-fit-content;   
   max-width: -moz-fit-content;   
   max-width: fit-content;
   padding: 8px 16px;
   border-radius: 5px;
   text-align: center;
   font-size: 14px;
   font-weight: 500;
   margin: 10px;
